
/* Default CSS */
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
  }
  
  html {
    scroll-behavior: smooth;
    scroll-padding-top: 75px;
  }
  
  body {
    font-weight: 400;
    overflow-x: hidden;
    position: relative;
    background-color: #2B2B2B !important;
    color: #000000 !important;
    font-family: "Centra", sans-serif !important;
  }
  
  h1,
  h2,
  h3,
  h4,
  h5,
  h6 {
    margin: 0;
    padding: 0;
    line-height: normal;
  }
  
  p,
  a,
  li,
  button,
  ul {
    margin: 0;
    padding: 0;
    line-height: normal;
    text-decoration: none;
  }

  h2 {
    text-align: center;
  }
  
  a:hover {
    text-decoration: none;
  }
  
  img {
    width: 100%;
    height: auto;
  }
  
  button {
    border: 0;
    background-color: transparent;
  }
  
  input:focus,
  textarea:focus,
  select:focus {
    outline: none;
  }
  
  @media (min-width: 1700px) {
    main .container {
      max-width: 100%;
      padding: 0 150px;
    }
  }
  
  p.success {
    color: green;
  }
  
  p.danger {
    color: red;
  }
  

/* Footer Container */
.footer-container {
    background-color: #000000;
    color: #ffc629;
    padding: 2rem 0;
    margin-top: auto; /* For sticky footer */
    border-top: 3px solid #ffc629;
  }
  
  /* Footer Content */
  .footer-content {
    max-width: 1200px;
    margin: 0 auto;
    padding: 0 20px;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
  }
  
  /* Footer Sections */
  .footer-section {
    flex: 1;
    min-width: 250px;
    margin: 1rem;
  }
  
  .footer-section h4 {
    color: #ffc629;
    margin-bottom: 1rem;
    font-size: 1.2rem;
  }
  
  /* Footer Links */
  .footer-links {
    list-style: none;
    padding: 0;
  }
  
  .footer-links li {
    margin-bottom: 0.5rem;
  }
  
  .footer-links a {
    color: #B2B4B2;
    text-decoration: none;
    transition: color 0.3s ease;
  }
  
  .footer-links a:hover {
    color: #ffc629;
  }
  
  /* Footer Bottom */
  .footer-bottom {
    background-color: #2D2926;
    text-align: center;
    padding: 1rem 0;
    margin-top: 2rem;
    border-top: 1px solid #B2B4B2;
  }
  
  .footer-bottom p {
    margin: 0;
    color: #B2B4B2;
    font-size: 0.9rem;
  }
  
  /* Social Icons */
  .social-icons {
    display: flex;
    gap: 1rem;
    margin-top: 1rem;
  }
  
  .social-icons a {
    color: #B2B4B2;
    font-size: 1.5rem;
    transition: color 0.3s ease;
  }
  
  .social-icons a:hover {
    color: #ffc629;
  }
  /* Project pages work*/
.box {
    background: #ffffff;
    border-radius: 64px;
    padding: 20px 20px 20px 20px;
    margin-top: 60px;
    margin-left: 10%;
    margin-right: 10%;
    }
  
  .box h1 {
    padding: 0px 0px 20px 0px;
  }
  
  .box h3 {
    padding: 15px 0px 0px 0px;
  }
  
  .box img {
    display: block;
    margin: auto;
    max-width: 800px;
    border-radius: 24px;
    padding-top: 10px;
    padding-bottom: 40px;
  }
  .box div {
    font-size: 17px;
    text-wrap: wrap;
    text-align: justify;
    padding: 10px 3px 30px 0px;
  }
  .Logo {
    width: 400px;
    align-self: center;  
    padding-bottom: 50px;
  }
  .team-container {
  display: flex;
  justify-content: center;
  gap: 40px;
}

.team-member {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 200px;
}

/* Circular container */
.team-member-image {
  width: 150px;
  height: 250px;
}

/* Image fills the circle */
.team-member-image img {
  width: 100%;
  height: 100%;
  object-fit: cover;      /* center crop */
  /* no border-radius here */
}

.team-member-name {
  margin-top: 12px;
  font-size: 1rem;
  text-align: center;
}

  .child {
    display: inline-block;
    margin-left: 50px;
    margin-right: 50px;
    vertical-align: middle;
    align-items: center;
  }

  /* container */
.navbar {
  position: fixed;
  top: 0;
  width: 100%;
  padding: 18px 50px;
  background-color: #000000;
  display: flex;
  align-items: center;
  justify-content: space-between;
  transition: padding 0.3s, background-color 0.3s;
  z-index: 1000;
}

.navbar.scrolled {
  padding: 8px 50px;
  background-color: #000;
}

/* left (logo) */
.navbar-left .logo {
  height: 48px;
  width: auto;
}

/* center (links) */
.navbar-center {
  display: flex;
  list-style: none;
  margin: 0;
  padding: 0;
  gap: 40px;
}

.navbar-center .nav-link {
  color: #fff;
  text-decoration: none;
  font-size: 18px;
  opacity: 0.75;
  transition: opacity 0.2s;
}

.navbar-center .nav-link:hover,
.navbar-center .nav-link.active {
  opacity: 1;
  color: #ffc629;
}

